.container1 {
    text-align: center;  
}
.container1 p {
    text-align: left;  
    justify-content: center;
    margin-left: 10%;
    margin-right: 10%;
}

.heading {
    text-align: center;
}
.container2 {
    justify-content: center;
    display: grid;
    gap: 10px 10px;
    grid-template-columns: repeat(3, 350px);   
}

.White-Box {
    text-align: center;
    background-color: white;
    height: 250px;
    width: 350px; 
    position: relative;
    overflow: hidden;
}

.Black-Box {
    justify-content: center;
    text-align: center;
    background-color: black;
    color: white;
    height: 250px;
    width: 350px;
    position: relative;
    overflow: hidden;
}
.White-Box img, .Black-Box img {
    justify-content: center;
    height: 100px;
    width: 100px;
    padding: 20px;
}
.service-description {
    opacity: 0;
} 

.White-Box:hover,
.Black-Box:hover {
    background-color: #f4b400;
    color: black;
    transform: translateY(-15px);
    cursor: pointer; 
    border-radius: 5px;
}
.White-Box:hover .service-description,
.Black-Box:hover .service-description {
    opacity: 5;
    max-height: 200px;
}

.White-Box:hover img, .Black-Box:hover img {
   display: none ;
}

